/* Navbar */

.nav {
	padding: 20px 0;
	background-color: var(--nav-bg);
	border-bottom: 1px solid var(--nav-border);
	color: var(--nav-text);
	letter-spacing: normal;

	.logo {
		margin-right: auto;
		color: var(--nav-text);
		font-size: 24px;
		font-family: 'Poppins', sans-serif;
		& strong {
			font-weight: 700;
		}
	}

	&-row {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		column-gap: 30px;
		row-gap: 20px;
		flex-wrap: wrap;
	}

	&-list {
		display: flex;
		flex-wrap: wrap;
		row-gap: 10px;
		align-items: center;
		column-gap: 40px;
		font-size: 16px;
		font-weight: 500;
		font-family: 'Poppins', sans-serif;

		&__link {
			color: var(--nav-text);
			transition: opacity 0.2s ease-in;
			&:hover {
				opacity: 0.8;
			}
			&--active {
				position: relative;
			}

			&--active::before {
				content: '';

				position: absolute;
				left: 0;
				top: 100%;

				display: block;
				height: 2px;
				width: 100%;
				background-color: var(--accent);
			}
		}
	}
}
